.ArticleCard {
    width: 350px;
    margin-bottom: 20px;
    // border: 1px solid red;
    padding: 15px;
    // height: 500px;

    .inner {
        display: flex;
        flex-direction: column;
    }

    .top {
        border: 3px solid #4E3B1F;
        border-radius: 10px;
        padding: 15px;
        box-shadow: 10px 5px rgba(0, 0, 0, 0.5);
        height: 296px;

        h1 {
            font-family: "SF Pro Text Regular";
            font-size: 32px;
            font-weight: 400;
            // border: 1px solid black;
            // width: ;
            margin: 0;
        }

        p {
            font-family: "SF Pro Text Regular";
            font-size: 16px;
        }
    }

    .bottom {
        margin-top: 20px;

        button {
            width: 157px;
            height: 35px;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;
            background: none;
            border: 1px solid #424135D9;
            border-radius: 5px;
            cursor: pointer;
            transition: 0.45s;

            img {
                width: 32px;
            }

            h5 {
                font-family: "SF Pro Text Regular";
                font-size: 16px;
                font-weight: 300;
            }

            &:hover,
            &:focus {
                // background: #424135D9;
                box-shadow:
                    0 0 0 0 #424135D9,
                    inset 0 -4.5em 0 0 #424135D9;
            }

            &:hover {
                color: #E2E1CA;

                img {
                    filter: invert(100%);
                }
            }
        }
    }
}